
import React,{Component} from "react";
import Slider from "../Slider/Slider"
import '../../common/style/base.css'
import './contactus.css'
import { Link } from "react-router-dom"
import { database } from '../../firebase'




  class ContactUs extends Component {
    constructor(props) {
      super(props);
        
      this.state = {
        index: 0,
        messages: [],
        newMessage: {
          author: "",
          text: "",
          email: "",
          date: "",
          id: "",
          avatar: "../img/unknown-avatar.png",
         
        }
      }
    }


 

    handleAuthorChange = (e) => {
      this.setState({
        newMessage:
        {
          ...this.state.newMessage,
          author: e.target.value
        }
      })
    }
    handleTextChange = (e) => {
      this.setState({
        newMessage:
        {
          ...this.state.newMessage,
          text: e.target.value
        }
      })
    }
    handleEmailChange = (e) => {
      this.setState({
        newMessage: {
          ...this.state.newMessage,
          email: e.target.value
        }
      })
    }
    onSend = (e) => {
      e.preventDefault()
      this.setState({
        newMessage:
        {
          ...this.state.newMessage,
          date: new Date().toLocaleString(),
          id: this.state.messages.length,
        }
      })
        
      this.setState({
        messages: [...this.state.messages,
        this.state.newMessage]
      })
        
        // –ó–∞–ø–∏—Å—å –≤ –±–∞–∑—É COMMENT
        //–ø–æ–∏—Å–∫ –ø–æ—Å–ª–µ–¥–Ω–µ–π –∑–∞–ø–∏—Å–∏
        const lastCommentRef = database
          .ref(`/contactUsMessage/`)
          .limitToLast(1)
        
        // –¥–µ–ª–∞–µ–º —Å–Ω–∏–º–æ–∫ –±–∞–∑—ã –ø–æ—Å–ª–µ–¥ message. –µ–≥–æ –∏–Ω–¥–µ–∫—Å –∏ –∑–∞–ø–∏—Å—å –≤ state
        lastCommentRef.on("value", (d) => {
          const ind = +(Object.keys(d.val())[0]) + 1
          this.setState({ index: ind })
        }, (err) => { console.log("Error: " + err.code) })
      
        // –∑–∞–ø–∏—Å—å –≤ –±–∞–∑—É –Ω–æ–≤–æ–≥–æ message —Å –æ–∂–∏–¥–∞–Ω–∏–µv –æ–±–Ω–æ–≤–ª–µ–Ω–∏—è state
        setTimeout(() => {
          database.ref(`/contactUsMessage`)
            .child(`${this.state.index}`)
            .set(this.state.newMessage)
        }, 300)
     
  // –æ–±–Ω—É–ª–µ–Ω–∏–µ –ø–æ–ª–µ–π —Ñ–æ—Ä–º—ã —Å –æ–∂–∏–¥–∞–Ω–∏–µ–º
      setTimeout(() => {
        this.setState({
          newMessage: {
            author: "",
            text: "",
            email: "",
          }
        })
        
      }, 400)

alert('–í–∞—à–µ —Å–æ–æ–±—â–µ–Ω–∏–µ —É—Å–ø–µ—à–Ω–æ –æ—Ç–ø—Ä–≤–ª–µ–Ω–æ')

    }
    
    render() {
      return (
        <>
          <Slider />
          <form method="post" className="contact-us-form"
          onSubmit={this.onSend}
          >
            <label htmlFor='author'>
              <br />
                                –í–∞—à–µ –∏–º—è <small>(–æ–±—è–∑–∞—Ç–µ–ª—å–Ω–æ–µ –ø–æ–ª–µ)</small>
              <br />
              <input
                onChange={this.handleAuthorChange}
                type='text'
                value={this.state.newMessage.author}
                id='author'
               class = 'author'
                name='author'
                placeholder="–í–≤–µ–¥–∏—Ç–µ –≤–∞—à–µ –∏–º—è..."
                required
             />
              <br />
            </label>
          
            <label htmlFor='comment'>
              –¢–µ–∫—Å—Ç –í–∞—à–µ–≥–æ –æ–±—Ä–∞—â–µ–Ω–∏—è –∫ –Ω–∞–º: <small>(–æ–±—è–∑–∞—Ç–µ–ª—å–Ω–æ–µ –ø–æ–ª–µ)</small>
              <br />
              <textarea
                onChange={this.handleTextChange}
                value={this.state.newMessage.text}
                name="comment"
                id="text-area"
                className = 'text-area'
                placeholder="–í–≤–µ–¥–∏—Ç–µ –≤–∞—à —Ç–µ–∫—Å—Ç..."
                required></textarea>
            </label>
                       
            <br />
            <label htmlFor='email'>
              Email <small>(–æ–±—è–∑–∞—Ç–µ–ª—å–Ω–æ–µ –ø–æ–ª–µ)</small>
              <br />
              <input
                onChange={this.handleEmailChange}
                type='email'
                value={this.state.newMessage.email}
                id='email'
                className = 'email'
                name='author_email'
                placeholder="–í–≤–µ–¥–∏—Ç–µ –≤–∞—à email..."
                required />
            </label>
            <br />
                      
            <div className='comment-btn'>
              {/* <Link to="/"> */}
                <button
                
                type="submit"
                value="send message" >
                  –û—Ç–ø—Ä–∞–≤–∏—Ç—å —Å–æ–æ–±—â–µ–Ω–∏–µ
                                </button>
              {/* </Link> */}
            </div>
          </form>
                
        </>
		
      )
    }
  }

export default ContactUs