
import React, {Component} from 'react'
import { database } from '../../firebase'
import './commentform.css'


class CommentForm extends Component {
    constructor(props) {
        super(props);
        localStorage.removeItem('firebase:previous_websocket_failure')
        this.state = {
           
            partCommentRefer:"",
        index: 0,
            testimonials:[],
            newTestimonials: {
                author: "",
                text: "",
                email: "",
                date: "",
                id: "",
                avatar: "../img/unknown-avatar.png",
                replies:[]
            }
        }
       
        
    }

    handleAuthorChange = (e) => {
         this.setState({
            newTestimonials:
            {
                ...this.state.newTestimonials,
                author: e.target.value
            }
        })
    }
 handleTextChange = (e) => {
      this.setState({
            newTestimonials:
          {
               ...this.state.newTestimonials,
                text: e.target.value
            }
        })
 }
     handleEmailChange = (e) => {
         this.setState({
             newTestimonials: {
                 ...this.state.newTestimonials,
                email: e.target.value
            }})
     }
    onSend = (e) => {
        e.preventDefault()
        this.setState({
            newTestimonials:
            {
                ...this.state.newTestimonials,
                date: new Date().toLocaleString(),
                id: this.state.testimonials.length,
            }
        })
        
        this.setState({
            testimonials: [...this.state.testimonials,
            this.state.newTestimonials]
        })
        
        if (this.props.isComment) {
            
                // –∑–∞–ø–∏—Å—å –≤ –±–∞–∑—É –Ω–æ–≤–æ–≥–æ –∫–æ–º–µ–Ω—Ç–∞ —Å –æ–∂–∏–¥–∞–Ω–∏–µ –æ–±–Ω–æ–≤–ª–µ–Ω–∏—è state
                setTimeout(() => {
                    database.ref(`${this.props.rootPartPath}${this.props.itemId}/comments`)
                        .child(`${this.props.commentsLength}`)
                        .set(this.state.newTestimonials)
                }, 300)
            
        } else {
                setTimeout(() => {
                    database.ref(`${this.props.rootPartPath}${this.props.itemId}/comments/${this.props.comId}/replies`)
                        .child(`${this.props.repliesLength}`)
                        .set(this.state.newTestimonials)
                }, 300)
            }

            setTimeout(() => {
                this.setState({
                    newTestimonials: {
                        author: "",
                        text: "",
                        email: "",
                    }
                })
        
            }, 400)
        }
    
    render() {
     
        return (
            <>
                
                <div className="comment-form">
                    <form method="post" action="" onSubmit={this.onSend}>
                        <label htmlFor='comment'>
                            –í–∞—à –∫–æ–º–º–µ–Ω—Ç–∞—Ä–∏–π: <small>(–æ–±—è–∑–∞—Ç–µ–ª—å–Ω–æ–µ –ø–æ–ª–µ)</small>
                            <br />
                            <textarea onChange={this.handleTextChange} value={this.state.newTestimonials.text} name="comment" id="text-area" cols="10" rows="6" tabIndex="4" placeholder="–í–≤–µ–¥–∏—Ç–µ –≤–∞—à –∫–æ–º–º–µ—Ç–∞—Ä–∏–π..." required></textarea>
                        </label>
                        <label htmlFor='author'>
                            <br />
                                –í–∞—à–µ –∏–º—è <small>(–æ–±—è–∑–∞—Ç–µ–ª—å–Ω–æ–µ –ø–æ–ª–µ)</small>
                            <br />
                            <input onChange={this.handleAuthorChange} type='text' value={this.state.newTestimonials.author} id='author' name='author' placeholder="–í–≤–µ–¥–∏—Ç–µ –≤–∞—à–µ –∏–º—è..." required />
                        </label>
                        <br />
                        <label htmlFor='email'>
                            Mail <small>(–æ–±—è–∑–∞—Ç–µ–ª—å–Ω–æ–µ –ø–æ–ª–µ)</small>
                            <br />
                            <input onChange={this.handleEmailChange} type='email' value={this.state.newTestimonials.email} id='email' name='author_email' placeholder="–í–≤–µ–¥–∏—Ç–µ –≤–∞—à email..." required />
                        </label>
                        <br />
                      
                        <div className='comment-btn'>
                            <button onClick={this.props.changeReplyForm} type="submit"  value="Leave a comment" >
                                –û—Å—Ç–∞–≤–∏—Ç—å –∫–æ–º–º–µ–Ω—Ç–∞—Ä–∏–π
                                </button>
                        </div>
                    </form>
                </div>
            </>
                
        )
        
    }
}

export default CommentForm